.starship-card-container {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  max-width: 875px;
  margin-bottom: 20px;
}

.starship-paragraph {
  margin: 17px 0;
}

.starship-card-header {
  margin-bottom: 20px;
}

.starship-card,
.starship-description {
  padding: 10px;
  margin: 10px;
  margin-right: 0;
  background-color: #fb641a;
  border-radius: 10px;
}

.starship-description {
  padding-right: 0;
  background-color: transparent;
}

@media (max-width: 900px) {
  .starship-paragraph {
    margin: 0;
    display: flex;
  }

  .starship-description {
    margin-left: 0;
    padding-left: 0;
  }
}